html{
    width: 100%;
    min-width: 320px;
    max-width: 1920px;
    background-color: white;
   margin: 0 auto;
}

.container{
    max-height: 100%;
    max-width: 90%;
    margin:0 auto;
    box-sizing: border-box;
}

.radius{
    position: relative;
    top: -25px;
   height: 25px;
   border-radius: 25px 25px 0 0;
   background-color: #ffffff;
   
}

.radius-end{
    position: relative;
    top: 25px;
   height: 25px;
   border-radius: 0 0 25px 25px ;
   background-color: #ffffff;
   
}

.none{
    display: none;
}

.aside {
    position: relative;
    top: -50px;
    display: none;
    background: #c59d9d;
    padding: 10px;
    width: 350px;
    height:calc(100vh - 170px);
    float: left;
    z-index: 5;
   }

   #menu:checked~.aside{
    display: block;
   }

.button{
    display: block;
    cursor: pointer;
    margin: 20px auto;
    border: 2px solid #cb11ab;
    color: #cb11ab;
    padding: 10px 20px;
}